.container-Adidas{
    min-height: 100vh;
    display: flex;
    align-items: center;
    gap:15px;
    padding: 20px 9%;
    position: relative;
    overflow: hidden;;
}
.container-Adidas .content-Adidas{
    flex: 1 1 400px;

}
.container-Adidas .img-Adidas{
    flex: 1 1 400px;
    position: relative;

}
.container-Adidas .img-Adidas .shoes{
    /* width: 100%;
    outline:none;*/
    height:70vh; 

}
.container-Adidas .img-Adidas::before{
    content: '';
    position: absolute;
    top:-50px;
    left: 50%;
    border-radius: 50%;;
    height: 450px;
    background: #fffbfb;
}

.container-Adidas .content-Adidas h3{
    color: #333;
    font-size: 50px;
}

.container-Adidas .content-Adidas p{
    color: #666;
    font-size: 17px;
    padding: 5px 0;
}
.container-Adidas h1{
  
}

.container-Adidas .content-Adidas .btn{
    display: inline-block;
    margin-top: 10px;
    font-size: 17px;
    padding: 9px 40px;
    background-color: #333;
    color: #fff;
    ;
}

.container-Adidas .content-Adidas .btn:hover{
    background: #666;

}
