.product-details {
    margin-top: 70px !important;
    position: relative;
    left:20px;
  }
  
  .product-details-info {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
    position: absolute;
    /* left:  550px; */
    left:750px;
  }
  .img-container-productDetails .imageMagnifier:hover .product-details-info{
    left:700px;
  }

  .product-details-info h6 {
    margin-bottom: 20px !important;
  }
 
  .product-details .img-container-productDetails{
    box-shadow: 3px 3px 4px rgba(0,0,0,0.1);
    position: relative;
    width:500px;
    height:355px;

  }
 

 .product-details .img-container-productDetails .imageMagnifier{
  position:absolute;
  top:0;
  left:30px;
  width:70%;
  height:70%;
  /* background-repeat: no-repeat;
  background-size: cover; */
  object-fit: cover;
  /* border: 1px solid #ccc; */
  /* box-shadow: 0 0 5px rgba(0,0,0,0.3); */

  
 }
 .product-details .img-container-productDetails .imageMagnifier:hover + .product-details-info{

 background-color: black;
}
  
 

  /* similiar products style */
  .similar-products .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
    cursor: pointer;

  }
  
  .similar-products .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .similar-products .card-price {
    color: green;
    font-weight: bold;
  }
  .similar-products button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  
  .similar-products .card-text {
    color: rgb(90, 89, 89);
  }
  
  .similar-products .card img:hover {
    /* transform: scale(0.9); */
  }
  .similar-products .card-img-top {
    height: 300px !important;
  }
  
  .similar-products .card:hover, .product-details .card img:hover{
    box-shadow: 0 4px 80px 0 rgba(0,0,0,0.1) , 4px 6px 25px 4px rgba(0,0,0,0.19);

  }


 