.home .home-slider .item{
  position: relative;
}

.home .home-slider .item img{
    height: 100vh;
    object-fit: cover;
    width: 100%;
}
.home .home-slider .item .content{
    position: absolute;
    top:50%;
    left:5%;
    transform: translateY(-50%);
}
.home .home-slider .item .content h3{
    color:#fff;
    text-shadow: 0 .3rem .5rem #000;
    font-size: 5rem;
    text-transform: uppercase;
}
.home .home-slider .item .content p{
    color:#fff;
    font-size: 2.5rem;
    padding: 1rem 0;
    width: 50rem;

}
.home .home-slider .item .btn{
    height: 4rem;
    width: 14rem;
    background:blue;
    color: #fff;
    font-size: 2rem;
    cursor: pointer;
    border: none;
    outline: none;
}

.home .home-slider .item .btn:hover{
    letter-spacing: .2rem;
    transform: scale(0.9);
    opacity: .4;
}

