.Search h1{
 margin-top: 53px;
 font-family: "poppins","sans-sarif";
}
.Search h4{
    color: green;
}
.Search{
    display: flex;
    flex-wrap: wrap;
}
.Search .card {
    background-color: rgba(128, 128, 128, 0.097);
    width:18rem;
    cursor: pointer;
    /* transition: all 0.5s ease 0s; */
  }


  /*  */
  
  .Search .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .Search .card-price {
    color: green;
    font-weight: bold;
  }
  .Search button {
    border-radius:15px;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  .Search .card:hover{
    box-shadow: 0 4px 80px 0 rgba(0,0,0,0.1) , 4px 6px 25px 4px rgba(0,0,0,0.19);

  }

  .Search .box-pro{
   opacity: 0;
   transition: 1s ease-in-out;

  }
  
  .Search .card:hover .box-pro{
    opacity: 1;

  }
  .Search .card-text {
    color: rgb(90, 89, 89);
  }
  
  /* .Search .card img:hover {
    transform: scale(0.9);
  } */
  
  .Search .card-img-top {
    height: 200px !important; 
    /* height :300 */
  }

  .Search .card-title{
    color: rgb(97, 94, 94);
    font-family: "Playfair Display", serif;
  }
  .Search .card-title{
    color: black;
    
  }