.banner-img {
    margin-top: 60px;
  }
  
  .home-page h1 {
    color: gray;
    font-family: "Playfair Display", serif;
    
  }
  
  .home-page {
    display: flex;
    flex-wrap: wrap;
  }
  .home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width:18rem;
    cursor: pointer;
    /* transition: all 0.5s ease 0s; */
  }

  .home-page .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .home-page .card-price {
    color: green;
    font-weight: bold;
  }
  .home-page button {
    border-radius:15px;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
  }
  .home-page .card:hover{
    box-shadow: 0 4px 80px 0 rgba(0,0,0,0.1) , 4px 6px 25px 4px rgba(0,0,0,0.19);

  }

  .home-page .box-pro{
   opacity: 0;
   transition: 1s ease-in-out;

  }
  
  .home-page .card:hover .box-pro{
    opacity: 1;

  }
  .home-page .card-text {
    color: rgb(90, 89, 89);
  }
  
  /* .home-page .card img:hover {
    transform: scale(0.9);
  } */
  
  .home-page .card-img-top {
    height: 200px !important; 
    /* height :300 */
  }
  /* ==================
  filters
  ============== */
  .filters h4 {
    margin-top: 100px;
    color: gray !important;
    border-bottom: 1px solid rgba(128, 128, 128, 0.337);
  }
  
  .ant-checkbox-wrapper:first-of-type {
    margin-left: 8px;
  }
  
  .ant-checkbox-wrapper {
    margin: 2px;
  }
  .ant-radio-wrapper {
    margin-bottom: 5px !important;
    margin-left: 10px;
  }
  
  .filters button {
    background-color: black;
    width: 100%;
    border: none;
    border-radius: 0;
    margin-top: 20px;
  }
  
  .loadmore {
    color: green;
    font-weight: bold;
    font-size: 20px !important;
  }
  
.checked {
  color: orange;
}